$xxs: 320px;
$xs: 575px;
$sm: 767px;
$md: 991px;
$lg: 1199px;
$xl: 1440px;
$xxl: 1920px;

$headerHeight: 70px;

$colorDarkShade100: #1f2041;
$colorDarkShade75: rgba(31, 32, 65, 0.75);
$colorDarkShade50: rgba(31, 32, 65, 0.5);
$colorDarkShade25: rgba(31, 32, 65, 0.25);
$colorDarkShade15: rgba(31, 32, 65, 0.15);
$colorDarkShade5: rgba(31, 32, 65, 0.05);
$colorRed: #ff0000;
$colorPurple: #bc9cff;
$colorPurpleOpaque: rgba(188, 156, 255, 0.5);
$colorBlue: #8ba4f9;
$colorBlueOpaque: rgba(139, 164, 249, 0.5);
$colorGreen: #6fcf97;
$colorWhite: #ffffff;
$colorLightBlue: #66d2ea;
$gradientPurple: linear-gradient($colorPurple 0%, $colorBlue 100%);
$gradientGreen: linear-gradient($colorGreen 0%, $colorLightBlue 100%);

@mixin font-h1() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: $colorDarkShade100;
}

@mixin font-h2() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 19px;
  line-height: 23px;
  color: $colorDarkShade100;
}

@mixin font-h3() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: $colorDarkShade100;
  text-transform: uppercase;
}

@mixin font-body() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: $colorDarkShade75;
}

@mixin media-breakpoint-up($width) {
  @media (min-width: ($width + 1)) {
    @content;
  }
}

@mixin media-breakpoint-down($width) {
  @media (max-width: $width) {
    @content;
  }
}

@mixin media-breakpoint-between($min-width, $max-width) {
  @media (min-width: ($min-width + 1)) and (max-width: $max-width) {
    @content;
  }
}

html {
  height: 100%;
  width: 100%;
  font-size: 16px;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  width: 100%;
  background: rgb(255, 255, 255);
  min-width: 320px;
}

main {
  flex-grow: 1;
}

fieldset {
  padding: 0;
  margin: 0;
  border: none;
}

form {
  margin-block-end: 0;
}

button {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

image {
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

.checklist {
  * {
    box-sizing: border-box;
  }

  &_collapsing,
  &_temporarily-collapsing {
    .checklist__label {
      cursor: pointer;
    }
  }

  &:not(.checklist_collapsing) {
    .checklist__image {
      display: none;
    }
  }

  &:not(.checklist_collapsing).checklist_temporarily-collapsing {
    .checklist__image {
      display: block;
    }
  }

  &_expanded {
    .checklist__list-wrapper {
      display: block;
    }

    .checklist__image {
      transform: rotate(180deg);
      display: block;
    }
  }

  user-select: none;
  max-width: 266px;
  width: 100%;

  &__label {
    @include font-h3;
    position: relative;
  }

  &__image {
    position: absolute;
    right: 15px;
    top: 3px;
    transition: all 0.3s ease 0s;
  }

  &__list-wrapper {
    max-width: 266px;
    width: 100%;
    background-color: $colorWhite;
    position: absolute;
    z-index: 2;
    padding-top: 14px;
    display: none;
  }

  &__category {
    display: inline-flex;
    position: relative;

    &-wrapper {
      margin-bottom: 7px;
    }

    &-text {
      margin-left: 9px;
    }

    &-checkbox {
      position: absolute;
      clip: rect(0 0 0 0);
      width: 1px;
      height: 1px;
      margin: -1px;

      &:focus + .checklist__category-checkmark {
        border: 1px solid $colorPurple;
      }

      &:checked + .checklist__category-checkmark,
      &:checked:focus + .checklist__category-checkmark {
        border: 1px solid $colorPurple;
        background-image: url("@com/checklist/image/tip.svg");
        background-position-y: 42%;
        background-position-x: 40%;
        background-repeat: no-repeat;
      }
    }

    &-checkmark {
      cursor: pointer;
      display: inline-block;
      position: relative;
      border: 1px solid $colorDarkShade25;
      width: 18px;
      height: 18px;
      border-radius: 5px;
      display: inline-block;
      width: 18px;
      height: 18px;
      background-color: $colorWhite;
    }

    &-name {
      cursor: pointer;
      @include font-body;
      line-height: 19px;
      padding-top: 2px;
      max-width: 215px;
      &_rich {
        font-family: "Montserrat", Arial, Helvetica, sans-serif;
        font-weight: bold;
        margin-bottom: 5px;
      }
    }

    &-description {
      max-width: 230px;
      cursor: pointer;
      font-family: "Montserrat", Arial, Helvetica, sans-serif;
      font-weight: normal;
      font-size: 12px;
      line-height: 14px;
      color: $colorDarkShade75;
      padding-bottom: 2px;
    }
  }
}
