$xxs: 320px;
$xs: 575px;
$sm: 767px;
$md: 991px;
$lg: 1199px;
$xl: 1440px;
$xxl: 1920px;

$headerHeight: 70px;

$colorDarkShade100: #1f2041;
$colorDarkShade75: rgba(31, 32, 65, 0.75);
$colorDarkShade50: rgba(31, 32, 65, 0.5);
$colorDarkShade25: rgba(31, 32, 65, 0.25);
$colorDarkShade15: rgba(31, 32, 65, 0.15);
$colorDarkShade5: rgba(31, 32, 65, 0.05);
$colorRed: #ff0000;
$colorPurple: #bc9cff;
$colorPurpleOpaque: rgba(188, 156, 255, 0.5);
$colorBlue: #8ba4f9;
$colorBlueOpaque: rgba(139, 164, 249, 0.5);
$colorGreen: #6fcf97;
$colorWhite: #ffffff;
$colorLightBlue: #66d2ea;
$gradientPurple: linear-gradient($colorPurple 0%, $colorBlue 100%);
$gradientGreen: linear-gradient($colorGreen 0%, $colorLightBlue 100%);

@mixin font-h1() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: $colorDarkShade100;
}

@mixin font-h2() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 19px;
  line-height: 23px;
  color: $colorDarkShade100;
}

@mixin font-h3() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: $colorDarkShade100;
  text-transform: uppercase;
}

@mixin font-body() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: $colorDarkShade75;
}

@mixin media-breakpoint-up($width) {
  @media (min-width: ($width + 1)) {
    @content;
  }
}

@mixin media-breakpoint-down($width) {
  @media (max-width: $width) {
    @content;
  }
}

@mixin media-breakpoint-between($min-width, $max-width) {
  @media (min-width: ($min-width + 1)) and (max-width: $max-width) {
    @content;
  }
}

html {
  height: 100%;
  width: 100%;
  font-size: 16px;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  width: 100%;
  background: rgb(255, 255, 255);
  min-width: 320px;
}

main {
  flex-grow: 1;
}

fieldset {
  padding: 0;
  margin: 0;
  border: none;
}

form {
  margin-block-end: 0;
}

button {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

image {
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

.filter-form {
  @include media-breakpoint-down($xs) {
    box-sizing: border-box;
    position: absolute;
    z-index: 4;
    background: $colorWhite;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  @include media-breakpoint-down($xs) {
    min-width: 319px;
  }
  &__wrapper {
    box-sizing: border-box;

    @include media-breakpoint-down($lg) {
      padding: 20px 5px;
      box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
      border-radius: 4px;
      max-width: 100%;
      height: 195px;
      display: grid;
      column-gap: 9px;
      grid-template-rows: 45px 40px 40px 40px;
      grid-template-columns: auto auto auto;
      grid-template-areas:
        "calendar guests price-filter"
        "calendar guests price-filter"
        "house-rules facilities facilities-adv"
        "accessability facilities facilities-adv";
    }
    @include media-breakpoint-down($md) {
      height: 250px;
      grid-template-rows: 75px 35px 35px 33px 35px;
      grid-template-columns: auto auto;
      grid-template-areas:
        "calendar guests"
        "price-filter house-rules"
        "price-filter accessability"
        "price-filter facilities"
        "facilities-adv facilities-adv";
    }

    @include media-breakpoint-down($xs) {
      display: block;
      height: auto;
      max-width: 320px;
      box-shadow: none;
    }

    @include media-breakpoint-down($xxs) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    &_hidden {
      display: none;
    }
  }

  &__show-filter {
    display: none;
    @include media-breakpoint-down($xs) {
      align-self: flex-start;
      display: block;
      padding: 10px;
      display: flex;
      cursor: pointer;
    }
    &-image {
      width: 20px;
      height: 20px;
    }
  }

  &__calendar-wrapper {
    box-sizing: border-box;
    margin-bottom: 20px;
    @include media-breakpoint-down($lg) {
      margin: 0;
      grid-area: calendar;
    }
    @include media-breakpoint-down($xs) {
      margin-bottom: 20px;
    }
    @include media-breakpoint-down($xxs) {
    }
  }
  &__guests-wrapper {
    box-sizing: border-box;
    margin-bottom: 31px;
    @include media-breakpoint-down($lg) {
      margin: 0;
      grid-area: guests;
    }
    @include media-breakpoint-down($xs) {
      margin-bottom: 31px;
    }
  }
  &__price-filter-wrapper {
    box-sizing: border-box;
    margin-bottom: 23px;
    @include media-breakpoint-down($lg) {
      margin: 0;
      grid-area: price-filter;
    }
    @include media-breakpoint-down($xs) {
      margin-bottom: 23px;
    }
  }
  &__house-rules-wrapper {
    box-sizing: border-box;
    margin-bottom: 145px;
    @include media-breakpoint-down($lg) {
      grid-area: house-rules;
    }
    @include media-breakpoint-down($xs) {
      margin-bottom: 20px;
      display: block;
    }
  }
  &__accessability-wrapper {
    box-sizing: border-box;
    margin-bottom: 150px;
    @include media-breakpoint-down($lg) {
      grid-area: accessability;
    }
    @include media-breakpoint-down($xs) {
      margin-bottom: 20px;
    }
  }
  &__facilities-wrapper {
    box-sizing: border-box;
    margin-bottom: 30px;
    @include media-breakpoint-down($lg) {
      margin: 0;
      grid-area: facilities;
    }
    @include media-breakpoint-down($xs) {
      margin-bottom: 30px;
    }
  }

  &__facilities-adv-wrapper {
    padding-bottom: 50px;
    @include media-breakpoint-down($lg) {
      margin: 0;
      grid-area: facilities-adv;
    }
  }
}
