$xxs: 320px;
$xs: 575px;
$sm: 767px;
$md: 991px;
$lg: 1199px;
$xl: 1440px;
$xxl: 1920px;

$headerHeight: 70px;

$colorDarkShade100: #1f2041;
$colorDarkShade75: rgba(31, 32, 65, 0.75);
$colorDarkShade50: rgba(31, 32, 65, 0.5);
$colorDarkShade25: rgba(31, 32, 65, 0.25);
$colorDarkShade15: rgba(31, 32, 65, 0.15);
$colorDarkShade5: rgba(31, 32, 65, 0.05);
$colorRed: #ff0000;
$colorPurple: #bc9cff;
$colorPurpleOpaque: rgba(188, 156, 255, 0.5);
$colorBlue: #8ba4f9;
$colorBlueOpaque: rgba(139, 164, 249, 0.5);
$colorGreen: #6fcf97;
$colorWhite: #ffffff;
$colorLightBlue: #66d2ea;
$gradientPurple: linear-gradient($colorPurple 0%, $colorBlue 100%);
$gradientGreen: linear-gradient($colorGreen 0%, $colorLightBlue 100%);

@mixin font-h1() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: $colorDarkShade100;
}

@mixin font-h2() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 19px;
  line-height: 23px;
  color: $colorDarkShade100;
}

@mixin font-h3() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: $colorDarkShade100;
  text-transform: uppercase;
}

@mixin font-body() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: $colorDarkShade75;
}

@mixin media-breakpoint-up($width) {
  @media (min-width: ($width + 1)) {
    @content;
  }
}

@mixin media-breakpoint-down($width) {
  @media (max-width: $width) {
    @content;
  }
}

@mixin media-breakpoint-between($min-width, $max-width) {
  @media (min-width: ($min-width + 1)) and (max-width: $max-width) {
    @content;
  }
}

html {
  height: 100%;
  width: 100%;
  font-size: 16px;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  width: 100%;
  background: rgb(255, 255, 255);
  min-width: 320px;
}

main {
  flex-grow: 1;
}

fieldset {
  padding: 0;
  margin: 0;
  border: none;
}

form {
  margin-block-end: 0;
}

button {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

image {
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

.range-slider {
  &__wrapper {
    max-width: 266px;
  }

  &__title {
    white-space: nowrap;
    @include font-h3;
    width: 50%;

    &-wrapper {
      display: flex;
      justify-content: space-between;
      margin-bottom: -3px;
    }
  }

  &__price {
    display: flex;

    &-delimiter {
      width: 10px;
      text-align: center;
    }

    &-from {
      text-align: end;
    }

    &-to {
      text-align: start;
    }

    &-from,
    &-to,
    &-delimiter {
      border: none;
      font-size: 12px;
      text-transform: uppercase;
      @include font-body;
      font-size: 12px;
      line-height: initial;
    }
  }

  &__text {
    @include font-body;
    font-size: 12px;
  }

  .from:focus,
  .to:focus {
    outline: auto !important;
  }

  span.irs.irs--flat {
    .irs-line {
      cursor: pointer;
      width: 100%;
      height: 6px;
      background: $colorWhite;
      border: 1px solid $colorDarkShade25;
      box-sizing: border-box;
      border-radius: 3px;
    }

    .irs-handle {
      width: 14px;
      height: 14px;
      margin-top: -1px;
      background: linear-gradient(180deg, $colorGreen 0%, $colorLightBlue 100%);
      border: 2px solid $colorWhite;
      border-radius: 50%;
      box-sizing: border-box;
      outline: none;
      cursor: pointer;

      &:focus {
        outline: auto;
      }

      i {
        display: none;
      }
    }

    .irs-bar {
      cursor: pointer;
      height: 6px;
      background: linear-gradient(180deg, $colorGreen 0%, $colorLightBlue 100%);
      border-radius: 3px;
    }
  }
}
