$xxs: 320px;
$xs: 575px;
$sm: 767px;
$md: 991px;
$lg: 1199px;
$xl: 1440px;
$xxl: 1920px;

$headerHeight: 70px;

$colorDarkShade100: #1f2041;
$colorDarkShade75: rgba(31, 32, 65, 0.75);
$colorDarkShade50: rgba(31, 32, 65, 0.5);
$colorDarkShade25: rgba(31, 32, 65, 0.25);
$colorDarkShade15: rgba(31, 32, 65, 0.15);
$colorDarkShade5: rgba(31, 32, 65, 0.05);
$colorRed: #ff0000;
$colorPurple: #bc9cff;
$colorPurpleOpaque: rgba(188, 156, 255, 0.5);
$colorBlue: #8ba4f9;
$colorBlueOpaque: rgba(139, 164, 249, 0.5);
$colorGreen: #6fcf97;
$colorWhite: #ffffff;
$colorLightBlue: #66d2ea;
$gradientPurple: linear-gradient($colorPurple 0%, $colorBlue 100%);
$gradientGreen: linear-gradient($colorGreen 0%, $colorLightBlue 100%);

@mixin font-h1() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: $colorDarkShade100;
}

@mixin font-h2() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 19px;
  line-height: 23px;
  color: $colorDarkShade100;
}

@mixin font-h3() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: $colorDarkShade100;
  text-transform: uppercase;
}

@mixin font-body() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: $colorDarkShade75;
}

@mixin media-breakpoint-up($width) {
  @media (min-width: ($width + 1)) {
    @content;
  }
}

@mixin media-breakpoint-down($width) {
  @media (max-width: $width) {
    @content;
  }
}

@mixin media-breakpoint-between($min-width, $max-width) {
  @media (min-width: ($min-width + 1)) and (max-width: $max-width) {
    @content;
  }
}

html {
  height: 100%;
  width: 100%;
  font-size: 16px;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  width: 100%;
  background: rgb(255, 255, 255);
  min-width: 320px;
}

main {
  flex-grow: 1;
}

fieldset {
  padding: 0;
  margin: 0;
  border: none;
}

form {
  margin-block-end: 0;
}

button {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

image {
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

.dropdown__input_rounded.dropdown__input_expanded {
  border-color: $colorDarkShade50;
  border-bottom: none;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.dropdown__input_not-rounded.dropdown__input_expanded {
  border-color: $colorDarkShade50;
  border-bottom: none;
}

.dropdown {
  * {
    box-sizing: border-box;
  }
  user-select: none;
  position: relative;

  &_width_wide {
    max-width: 320px;
    width: 100%;
  }

  &_width_narrow {
    max-width: 266px;
    width: 100%;
  }

  &__label {
    display: block;
    width: 100%;
    &-text {
      @include font-h3;
    }
  }

  &__image {
    cursor: pointer;
    position: absolute;
    bottom: 17px;
    right: 16px;

    &_expanded {
      transform: rotate(180deg);
      transition: all 0.3s ease 0s;
    }

    &_collapsed {
      transform: rotate(0deg);
      transition: all 0.3s ease 0s;
    }
  }

  &__input {
    cursor: pointer;
    text-overflow: ellipsis;
    &::placeholder {
      color: $colorDarkShade25;
    }
    @include font-body;
    height: 44px;
    padding-left: 12px;
    padding-right: 90px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    border: 1px solid $colorDarkShade25;
    margin-top: 5px;
    width: 100%;
    display: block;
    &:hover {
      border-color: $colorDarkShade50;
    }
    &:focus {
      border-color: $colorDarkShade50;
      outline: none;
    }

    &_rounded {
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
  }

  &__list-wrapper {
    background-color: $colorWhite;
    width: 100%;
    position: absolute;
    z-index: 3;
    border: 1px solid $colorDarkShade50;
    box-sizing: border-box;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;

    &_hidden {
      display: none;
    }
  }

  &__category {
    @include font-h3;
    margin-left: 14px;
    &-wrapper {
      display: flex;
      justify-content: space-between;
      height: 37px;
      align-items: center;
    }
  }

  &__count {
    &-wrapper {
      box-sizing: border-box;
      margin-right: 5px;
      max-width: 94px;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &-increment,
    &-decrement {
      cursor: pointer;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      background-color: $colorWhite;
      border: 1px solid $colorDarkShade50;
      color: $colorDarkShade50;
      &[disabled] {
        cursor: initial;
        border: 1px solid $colorDarkShade25;
        color: $colorDarkShade25;
      }
    }

    &-value {
      @include font-h3;
    }
  }

  &__button {
    &_hidden {
      display: none;
    }
    &-clear {
      flex-grow: 1;
    }
  }

  &__buttons {
    display: flex;
    justify-content: flex-end;
    margin: 20px 7px 13px 14px;
  }
}
