$xxs: 320px;
$xs: 575px;
$sm: 767px;
$md: 991px;
$lg: 1199px;
$xl: 1440px;
$xxl: 1920px;

$headerHeight: 70px;

$colorDarkShade100: #1f2041;
$colorDarkShade75: rgba(31, 32, 65, 0.75);
$colorDarkShade50: rgba(31, 32, 65, 0.5);
$colorDarkShade25: rgba(31, 32, 65, 0.25);
$colorDarkShade15: rgba(31, 32, 65, 0.15);
$colorDarkShade5: rgba(31, 32, 65, 0.05);
$colorRed: #ff0000;
$colorPurple: #bc9cff;
$colorPurpleOpaque: rgba(188, 156, 255, 0.5);
$colorBlue: #8ba4f9;
$colorBlueOpaque: rgba(139, 164, 249, 0.5);
$colorGreen: #6fcf97;
$colorWhite: #ffffff;
$colorLightBlue: #66d2ea;
$gradientPurple: linear-gradient($colorPurple 0%, $colorBlue 100%);
$gradientGreen: linear-gradient($colorGreen 0%, $colorLightBlue 100%);

@mixin font-h1() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: $colorDarkShade100;
}

@mixin font-h2() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 19px;
  line-height: 23px;
  color: $colorDarkShade100;
}

@mixin font-h3() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: $colorDarkShade100;
  text-transform: uppercase;
}

@mixin font-body() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: $colorDarkShade75;
}

@mixin media-breakpoint-up($width) {
  @media (min-width: ($width + 1)) {
    @content;
  }
}

@mixin media-breakpoint-down($width) {
  @media (max-width: $width) {
    @content;
  }
}

@mixin media-breakpoint-between($min-width, $max-width) {
  @media (min-width: ($min-width + 1)) and (max-width: $max-width) {
    @content;
  }
}

html {
  height: 100%;
  width: 100%;
  font-size: 16px;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  width: 100%;
  background: rgb(255, 255, 255);
  min-width: 320px;
}

main {
  flex-grow: 1;
}

fieldset {
  padding: 0;
  margin: 0;
  border: none;
}

form {
  margin-block-end: 0;
}

button {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

image {
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

.booking {
  box-sizing: border-box;
  background-color: $colorWhite;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  max-width: 380px;
  width: 100%;
  padding: 30px;

  &__details {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    &-price {
      margin-top: 6px;
    }
  }

  &__detail {
    &-mark {
      @include font-h3;
      padding-left: 1px;
    }

    &-number {
      @include font-h1;
      padding-left: 6px;
    }

    &-lux {
      @include font-h3;
      color: $colorPurple;
      padding-left: 9px;
    }

    &-price {
      @include font-body;
      font-weight: bold;
      color: $colorDarkShade50;
    }

    &-label {
      @include font-body;
      font-size: 12px;
      color: $colorDarkShade50;
      padding-left: 3px;
    }
  }

  &__date-dropdown {
    margin-top: 19px;
  }

  &__guests {
    margin-top: 20px;
  }

  &__price {
    &-breakdown {
      margin-top: 20px;
      @include font-body;
      line-height: 21px;
    }
    &-details {
      display: flex;
      justify-content: space-between;
      margin-bottom: 13px;
      position: relative;
      &:last-child {
        margin-top: 27px;
        .booking__price-name,
        .booking__price-cost {
          @include font-h2;
        }
      }
    }
    &-name {
      max-width: 220px;
    }

    &-info {
      position: absolute;
      cursor: pointer;
      top: 0;
      left: 220px;
    }

    &-total-filling {
      flex-grow: 1;
      flex-shrink: 1;
      border-bottom: 1px dashed $colorDarkShade25;
      margin-bottom: 5px;
    }
  }

  &__book-button {
    margin-top: 20px;
  }
  &-error {
    outline: 1px solid $colorRed;
  }
}
