$xxs: 320px;
$xs: 575px;
$sm: 767px;
$md: 991px;
$lg: 1199px;
$xl: 1440px;
$xxl: 1920px;

$headerHeight: 70px;

$colorDarkShade100: #1f2041;
$colorDarkShade75: rgba(31, 32, 65, 0.75);
$colorDarkShade50: rgba(31, 32, 65, 0.5);
$colorDarkShade25: rgba(31, 32, 65, 0.25);
$colorDarkShade15: rgba(31, 32, 65, 0.15);
$colorDarkShade5: rgba(31, 32, 65, 0.05);
$colorRed: #ff0000;
$colorPurple: #bc9cff;
$colorPurpleOpaque: rgba(188, 156, 255, 0.5);
$colorBlue: #8ba4f9;
$colorBlueOpaque: rgba(139, 164, 249, 0.5);
$colorGreen: #6fcf97;
$colorWhite: #ffffff;
$colorLightBlue: #66d2ea;
$gradientPurple: linear-gradient($colorPurple 0%, $colorBlue 100%);
$gradientGreen: linear-gradient($colorGreen 0%, $colorLightBlue 100%);

@mixin font-h1() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: $colorDarkShade100;
}

@mixin font-h2() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 19px;
  line-height: 23px;
  color: $colorDarkShade100;
}

@mixin font-h3() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: $colorDarkShade100;
  text-transform: uppercase;
}

@mixin font-body() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: $colorDarkShade75;
}

@mixin media-breakpoint-up($width) {
  @media (min-width: ($width + 1)) {
    @content;
  }
}

@mixin media-breakpoint-down($width) {
  @media (max-width: $width) {
    @content;
  }
}

@mixin media-breakpoint-between($min-width, $max-width) {
  @media (min-width: ($min-width + 1)) and (max-width: $max-width) {
    @content;
  }
}

html {
  height: 100%;
  width: 100%;
  font-size: 16px;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  width: 100%;
  background: rgb(255, 255, 255);
  min-width: 320px;
}

main {
  flex-grow: 1;
}

fieldset {
  padding: 0;
  margin: 0;
  border: none;
}

form {
  margin-block-end: 0;
}

button {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

image {
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

.footer {
  width: 100%;
  margin: 0 auto;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
  background-color: $colorWhite;

  &__wrapper {
    &-top {
      height: 272px;
      max-width: 1161px;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      @include media-breakpoint-down($lg) {
        padding-left: 20px;
        padding-right: 20px;
      }
      @include media-breakpoint-down($md) {
        padding: 0;
        justify-content: center;
        height: 162px;
      }
    }

    &-bottom {
      align-items: center;
      height: 70px;
      max-width: 1161px;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      @include media-breakpoint-down($lg) {
        padding-left: 20px;
        padding-right: 20px;
      }
      @include media-breakpoint-down($md) {
        padding-top: 18px;
        flex-direction: column;
        justify-content: start;
        padding-bottom: 118px;
      }
    }

    &-middle {
      height: 1px;
      background-color: $colorDarkShade15;
      @include media-breakpoint-down($md) {
        display: none;
      }
    }
  }

  &__items {
    flex-grow: 1;

    &-link,
    &-header {
      @include font-body;
      line-height: 17.07px;
      font-weight: bold;
      color: $colorDarkShade100;
      font-size: 12px;
      line-height: 14.63px;
      text-transform: uppercase;
      white-space: nowrap;
    }
    &:not(:first-child) {
      padding-top: 4px;
      @include media-breakpoint-down($md) {
        display: none;
      }
    }
    &:nth-child(1) {
      max-width: 270px;
      padding-right: 2.5%;
      @include media-breakpoint-down($lg) {
        padding-left: 5px;
        padding-right: 5px;
      }
    }
    &:nth-child(2) {
      padding-right: 5%;
      @include media-breakpoint-down($lg) {
        padding-left: 5px;
        padding-right: 5px;
      }
    }
    &:nth-child(3) {
      padding-right: 8%;
      @include media-breakpoint-down($lg) {
        padding-left: 5px;
        padding-right: 5px;
      }
    }
    &:nth-child(4) {
      padding-right: 4.5%;
      @include media-breakpoint-down($lg) {
        padding-left: 5px;
        padding-right: 5px;
      }
    }
    &:last-child {
      @include media-breakpoint-down($lg) {
        max-width: 270px;
      }
    }
    @include media-breakpoint-down($md) {
      display: flex;
      justify-content: center;
      align-items: flex-end;
    }
  }

  &__item {
    text-transform: initial;
    padding-top: 21px;
    white-space: nowrap;

    &-link {
      @include font-body;
      line-height: 17.07px;
      color: $colorDarkShade50;
    }
    @include media-breakpoint-down($md) {
      display: none;
    }
  }

  &__long-text {
    cursor: initial;
    white-space: initial;
    @include font-body;
    line-height: 17.07px;
    line-height: 22px;
  }

  &__social-links {
    display: flex;
    justify-content: space-between;
    max-width: 114px;
    width: 100%;

    @include media-breakpoint-down($md) {
      padding-top: 25px;
    }
  }
}
