$xxs: 320px;
$xs: 575px;
$sm: 767px;
$md: 991px;
$lg: 1199px;
$xl: 1440px;
$xxl: 1920px;

$headerHeight: 70px;

$colorDarkShade100: #1f2041;
$colorDarkShade75: rgba(31, 32, 65, 0.75);
$colorDarkShade50: rgba(31, 32, 65, 0.5);
$colorDarkShade25: rgba(31, 32, 65, 0.25);
$colorDarkShade15: rgba(31, 32, 65, 0.15);
$colorDarkShade5: rgba(31, 32, 65, 0.05);
$colorRed: #ff0000;
$colorPurple: #bc9cff;
$colorPurpleOpaque: rgba(188, 156, 255, 0.5);
$colorBlue: #8ba4f9;
$colorBlueOpaque: rgba(139, 164, 249, 0.5);
$colorGreen: #6fcf97;
$colorWhite: #ffffff;
$colorLightBlue: #66d2ea;
$gradientPurple: linear-gradient($colorPurple 0%, $colorBlue 100%);
$gradientGreen: linear-gradient($colorGreen 0%, $colorLightBlue 100%);

@mixin font-h1() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: $colorDarkShade100;
}

@mixin font-h2() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 19px;
  line-height: 23px;
  color: $colorDarkShade100;
}

@mixin font-h3() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: $colorDarkShade100;
  text-transform: uppercase;
}

@mixin font-body() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: $colorDarkShade75;
}

@mixin media-breakpoint-up($width) {
  @media (min-width: ($width + 1)) {
    @content;
  }
}

@mixin media-breakpoint-down($width) {
  @media (max-width: $width) {
    @content;
  }
}

@mixin media-breakpoint-between($min-width, $max-width) {
  @media (min-width: ($min-width + 1)) and (max-width: $max-width) {
    @content;
  }
}

html {
  height: 100%;
  width: 100%;
  font-size: 16px;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  width: 100%;
  background: rgb(255, 255, 255);
  min-width: 320px;
}

main {
  flex-grow: 1;
}

fieldset {
  padding: 0;
  margin: 0;
  border: none;
}

form {
  margin-block-end: 0;
}

button {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

image {
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

.header {
  width: 100%;
  box-sizing: border-box;
  height: $headerHeight;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
  background-color: $colorWhite;
  // position: absolute;
  z-index: 10;
  @include media-breakpoint-down($sm) {
    position: relative;
  }
  @include media-breakpoint-down($xs) {
    height: auto;
    align-items: flex-start;
  }

  &__wrapper {
    box-sizing: border-box;
    max-width: 1161px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    @include media-breakpoint-down($md) {
      width: 100%;
      margin: 0 20px 0 40px;
      display: flex;
      align-items: center;
      background-color: $colorWhite;
    }
    @include media-breakpoint-down($xs) {
      flex-direction: column;
      padding-top: 50px;
      padding-bottom: 20px;
    }
  }

  &__nav {
    &-level1 {
      display: flex;
      flex-grow: 1;
      @include media-breakpoint-down($md) {
        display: none;
        position: absolute;
        background-color: $colorWhite;
        left: 0;
        top: 45px;
        z-index: 1;
      }

      &_active {
        @include media-breakpoint-down($md) {
          display: block;
          z-index: 99;
        }
      }

      &-items {
        margin-left: auto;
        display: flex;
        align-items: center;
        @include media-breakpoint-down($md) {
          display: block;
          padding-left: 20px;
        }
      }
      &-item {
        position: relative;
        white-space: nowrap;
        padding-right: 24px;
        @include media-breakpoint-down($md) {
          margin-top: 20px;
        }

        &_active {
          font-weight: bold;
        }

        &-image {
          cursor: pointer;
          padding-left: 3px;
          @include media-breakpoint-down($md) {
            display: none;
          }
        }

        &-link {
          @include font-body;
        }
      }
    }

    &-level2 {
      position: absolute;
      z-index: 1;
      background-color: $colorWhite;
      display: none;

      &-item {
        margin-top: 10px;

        &-link {
          @include font-body;
        }

        &_expanded {
          padding-left: 20px;
          padding-right: 20px;
          display: block;
          @include media-breakpoint-down($md) {
            display: none;
          }
        }
      }
    }
  }

  &__logo {
    @include media-breakpoint-down($lg) {
      padding-left: 20px;
    }
    @include media-breakpoint-down($md) {
      top: -55px;
      left: 50%;
    }
    @include media-breakpoint-down($xs) {
      padding-left: 0;
      margin-bottom: 20px;
    }
  }

  &__auth {
    display: flex;

    &_is-authorized {
      &_false {
        flex-basis: 26%;
        @include media-breakpoint-down($lg) {
          flex-basis: 30%;
        }
        @include media-breakpoint-down($md) {
          flex-basis: 40%;
        }
        @include media-breakpoint-down($sm) {
          flex-basis: 50%;
        }

        @include media-breakpoint-down($xs) {
          display: flex;
          flex-direction: column;
        }
      }
    }

    @include media-breakpoint-down($lg) {
      margin-right: 20px;
    }
    @include media-breakpoint-down($md) {
      top: 0;
      left: 50%;
      margin-left: 20px;
    }
    @include media-breakpoint-down($xs) {
      flex-direction: column;
      height: 90px;
      justify-content: space-between;
      align-items: center;
    }
  }

  &__button {
    &-reg,
    &-login {
      margin-top: 3px;
      flex-grow: 1;
    }

    &-reg {
      padding-left: 20px;
      @include media-breakpoint-down($xs) {
        padding-left: 0;
      }
    }
  }

  &__profile {
    border-left: 1px solid rgba(31, 32, 65, 0.1);
    align-self: center;
    line-height: 30px;
    @include media-breakpoint-down($md) {
      border: none;
    }

    &-name {
      @include font-body;
      padding-left: 20px;
      white-space: nowrap;
      @include media-breakpoint-down($md) {
        padding: 0;
      }
    }
  }

  &__burger {
    border-radius: 1px;

    &-button {
      display: none;
      cursor: pointer;
      @include media-breakpoint-down($md) {
        display: block;
        position: absolute;
        width: 20px;
        height: 20px;
        left: 10px;
      }
      @include media-breakpoint-down($xs) {
        top: 20px;
      }
      &:before,
      &:after {
        @include media-breakpoint-down($md) {
          border-radius: 1px;
          content: "";
          background-color: $colorPurple;
          position: absolute;
          width: 100%;
          height: 2px;
          left: 0;
          transition: all 0.5s ease 0s;
        }
      }
      &:before {
        @include media-breakpoint-down($md) {
          top: 0;
          background-color: $colorPurple;
        }
      }
      &:after {
        @include media-breakpoint-down($md) {
          bottom: 0;
          background-color: $colorPurple;
        }
      }
    }
  }

  &__burger-button &__burger {
    @include media-breakpoint-down($md) {
      position: absolute;
      background-color: $colorPurple;
      left: 0;
      width: 100%;
      height: 2px;
      top: 9px;
      transition: all 0.9s ease 0s;
    }
  }

  &__burger-button.header__burger-button_active:before {
    @include media-breakpoint-down($md) {
      transform: rotate(45deg);
      top: 9px;
    }
  }

  &__burger-button.header__burger-button_active:after {
    @include media-breakpoint-down($md) {
      transform: rotate(-45deg);
      bottom: 9px;
    }
  }

  &__burger-button.header__burger-button_active &__burger {
    @include media-breakpoint-down($md) {
      transform: scale(0);
    }
  }
}
