$xxs: 320px;
$xs: 575px;
$sm: 767px;
$md: 991px;
$lg: 1199px;
$xl: 1440px;
$xxl: 1920px;

$headerHeight: 70px;

$colorDarkShade100: #1f2041;
$colorDarkShade75: rgba(31, 32, 65, 0.75);
$colorDarkShade50: rgba(31, 32, 65, 0.5);
$colorDarkShade25: rgba(31, 32, 65, 0.25);
$colorDarkShade15: rgba(31, 32, 65, 0.15);
$colorDarkShade5: rgba(31, 32, 65, 0.05);
$colorRed: #ff0000;
$colorPurple: #bc9cff;
$colorPurpleOpaque: rgba(188, 156, 255, 0.5);
$colorBlue: #8ba4f9;
$colorBlueOpaque: rgba(139, 164, 249, 0.5);
$colorGreen: #6fcf97;
$colorWhite: #ffffff;
$colorLightBlue: #66d2ea;
$gradientPurple: linear-gradient($colorPurple 0%, $colorBlue 100%);
$gradientGreen: linear-gradient($colorGreen 0%, $colorLightBlue 100%);

@mixin font-h1() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: $colorDarkShade100;
}

@mixin font-h2() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 19px;
  line-height: 23px;
  color: $colorDarkShade100;
}

@mixin font-h3() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: $colorDarkShade100;
  text-transform: uppercase;
}

@mixin font-body() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: $colorDarkShade75;
}

@mixin media-breakpoint-up($width) {
  @media (min-width: ($width + 1)) {
    @content;
  }
}

@mixin media-breakpoint-down($width) {
  @media (max-width: $width) {
    @content;
  }
}

@mixin media-breakpoint-between($min-width, $max-width) {
  @media (min-width: ($min-width + 1)) and (max-width: $max-width) {
    @content;
  }
}

html {
  height: 100%;
  width: 100%;
  font-size: 16px;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  width: 100%;
  background: rgb(255, 255, 255);
  min-width: 320px;
}

main {
  flex-grow: 1;
}

fieldset {
  padding: 0;
  margin: 0;
  border: none;
}

form {
  margin-block-end: 0;
}

button {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

image {
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

.form-elements {
  box-sizing: border-box;
  width: 100%;
  max-width: 1270px;
  margin: 0 auto;

  &__label {
    font-family: "Montserrat", Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 12px;
    line-height: 15px;
    color: #1f2041;
    text-transform: uppercase;
    display: inline-block;
  }

  &__wrapper {
    &-top {
      display: grid;
      grid-template-columns: 35% 31% auto;
      .form-elements__column {
        &:nth-child(1) {
          & > .form-elements__comp-wrapper {
            &:nth-child(2) {
              margin-top: 21px;
            }
            &:nth-child(3) {
              margin-top: 21px;
            }
            &:nth-child(4) {
              margin-top: 23px;
            }
            &:nth-child(5) {
              margin-top: 41px;
            }
            &:nth-child(6) {
              margin-top: 40px;
            }
            &:nth-child(7) {
              margin-top: 42px;
            }
            &:nth-child(8) {
              margin-top: 100px;
            }
            &:nth-child(9) {
              margin-top: 19px;
            }
            &:nth-child(10) {
              margin-top: 173px;
            }
            &:nth-child(11) {
              margin-top: 49px;
            }
          }
        }
        &:nth-child(2) {
          & > .form-elements__comp-wrapper {
            &:nth-child(1) {
              margin-top: 0px;
            }
            &:nth-child(2) {
              margin-top: 156px;
            }
            &:nth-child(3) {
              margin-top: 17px;
            }
            &:nth-child(4) {
              margin-top: 79px;
            }
            &:nth-child(5) {
              margin-top: 15px;
              display: flex;
              flex-direction: column;
              height: 54px;
              justify-content: space-between;
            }
            &:nth-child(6) {
              margin-top: 199px;
            }
            &:nth-child(7) {
              display: flex;
              max-width: 90px;
              justify-content: space-between;
              margin-top: 16px;
            }
            &:nth-child(9) {
              display: flex;
              max-width: 258px;
              justify-content: space-between;
              margin-top: 17px;
            }
            &:nth-child(10) {
              margin-top: 198px;
            }
            &:nth-child(11) {
              margin-top: 256px;
            }
          }
          & > .form-elements__label {
            &:nth-of-type(1) {
              margin-top: 155px;
            }
            &:nth-of-type(2) {
              margin-top: 37px;
            }
            &:nth-of-type(3) {
              margin-top: 45px;
            }
            &:nth-of-type(4) {
              margin-top: 41px;
            }
          }
        }
        &:nth-child(3) {
          & > .form-elements__comp-wrapper {
            &:nth-child(1) {
              margin-bottom: 36px;
            }
            &:nth-child(2) {
              margin-top: 0px;
            }
            &:nth-child(3) {
              margin-top: 14px;
            }
            &:nth-child(4) {
              margin-top: 29px;
            }
            &:nth-child(5) {
              margin-top: 32px;
            }
            &:nth-child(6) {
              margin-top: 30px;
              margin-bottom: 41px;
            }
            &:nth-child(7) {
              margin-top: 20px;
            }
            &:nth-child(8) {
              margin-top: 17px;
            }
            &:nth-child(9) {
              margin-top: 233px;
            }
            &:nth-child(10) {
              margin-top: 257px;
            }
          }
          > .form-elements__label {
            &:nth-of-type(1) {
              margin-top: -3px;
              margin-bottom: 2px;
            }
            &:nth-of-type(3) {
              margin-top: 257px;
              margin-bottom: 14px;
            }
          }
        }
      }
      @include media-breakpoint-down($md) {
        gap: 20px;
        display: flex;
        flex-wrap: wrap;
        padding-left: 20px;
        padding-right: 20px;
      }
    }

    &-bottom {
      display: grid;
      grid-template-columns: 35% auto;
      .form-elements__column {
        &:nth-child(1) {
          & > .form-elements__comp-wrapper {
            &:nth-child(1) {
              margin-top: 224px;
            }
            &:nth-child(2) {
              margin-top: 24px;
            }
          }
        }
        &:nth-child(2) {
          & > .form-elements__comp-wrapper {
            &:nth-child(1) {
              margin-top: 234px;
              @include media-breakpoint-down($md) {
                margin-top: 20px;
                margin-left: 0;
              }
            }
          }
        }
      }
      @include media-breakpoint-down($md) {
        display: flex;
        flex-wrap: wrap;
        padding-left: 20px;
        padding-right: 20px;
      }
    }
  }
}
