$xxs: 320px;
$xs: 575px;
$sm: 767px;
$md: 991px;
$lg: 1199px;
$xl: 1440px;
$xxl: 1920px;

$headerHeight: 70px;

$colorDarkShade100: #1f2041;
$colorDarkShade75: rgba(31, 32, 65, 0.75);
$colorDarkShade50: rgba(31, 32, 65, 0.5);
$colorDarkShade25: rgba(31, 32, 65, 0.25);
$colorDarkShade15: rgba(31, 32, 65, 0.15);
$colorDarkShade5: rgba(31, 32, 65, 0.05);
$colorRed: #ff0000;
$colorPurple: #bc9cff;
$colorPurpleOpaque: rgba(188, 156, 255, 0.5);
$colorBlue: #8ba4f9;
$colorBlueOpaque: rgba(139, 164, 249, 0.5);
$colorGreen: #6fcf97;
$colorWhite: #ffffff;
$colorLightBlue: #66d2ea;
$gradientPurple: linear-gradient($colorPurple 0%, $colorBlue 100%);
$gradientGreen: linear-gradient($colorGreen 0%, $colorLightBlue 100%);

@mixin font-h1() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: $colorDarkShade100;
}

@mixin font-h2() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 19px;
  line-height: 23px;
  color: $colorDarkShade100;
}

@mixin font-h3() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: $colorDarkShade100;
  text-transform: uppercase;
}

@mixin font-body() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: $colorDarkShade75;
}

@mixin media-breakpoint-up($width) {
  @media (min-width: ($width + 1)) {
    @content;
  }
}

@mixin media-breakpoint-down($width) {
  @media (max-width: $width) {
    @content;
  }
}

@mixin media-breakpoint-between($min-width, $max-width) {
  @media (min-width: ($min-width + 1)) and (max-width: $max-width) {
    @content;
  }
}

html {
  height: 100%;
  width: 100%;
  font-size: 16px;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  width: 100%;
  background: rgb(255, 255, 255);
  min-width: 320px;
}

main {
  flex-grow: 1;
}

fieldset {
  padding: 0;
  margin: 0;
  border: none;
}

form {
  margin-block-end: 0;
}

button {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

image {
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

$delta: 25px;
$sm-padding-left: 20px;
$sm-padding-bottom: 30px;
$sm-padding-top: 0;
$sm-padding-right: 20px;
$lg-padding-bottom: 30px;

@mixin padding-sm() {
  padding-top: $sm-padding-top;
  padding-left: $sm-padding-left;
  padding-bottom: $sm-padding-bottom;
  margin: 0;
}

.room-details-page {
  &__header {
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
  }

  &__room-info-header,
  &__room-impress-header,
  &__feedback-header-label,
  &__rules-header,
  &__cancel-header {
    @include font-h2;
  }
  @include media-breakpoint-down($xs) {
    box-sizing: border-box;
    padding-top: 40px;
    margin: 0 auto;
  }

  &__banner-wrapper {
    max-height: 485px;
    max-width: $xxl;
    width: 100%;
    box-sizing: border-box;
    margin: 0 auto;
    display: grid;
    grid-template-rows: 2fr 2fr;
    grid-template-columns: 2.75fr 1.25fr;
    grid-template-areas:
      "image-1 image-2"
      "image-1 image-3";
  }

  &__image {
    &-1 {
      max-height: 485px;
      box-sizing: border-box;
      grid-area: image-1;
      height: 100%;
      width: 100%;
      min-width: 100%;
      object-fit: cover;
    }

    &-2 {
      max-height: 242.5px;
      box-sizing: border-box;
      grid-area: image-2;
      height: 100%;
      width: 100%;
      min-width: 100%;
      object-fit: cover;
    }

    &-3 {
      max-height: 242.5px;
      box-sizing: border-box;
      grid-area: image-3;
      height: 100%;
      width: 100%;
      min-width: 100%;
      object-fit: cover;
    }
  }

  &__main-info-wrapper {
    max-width: 1162px;
    width: 100%;
    box-sizing: border-box;
    margin: 70px auto;
    display: grid;
    grid-template-rows: auto auto auto;
    grid-template-columns: 31% 36% auto;
    grid-template-areas:
      "info impress booking"
      "feedback feedback booking"
      "rules cancel booking";
    @include media-breakpoint-down($lg) {
      grid-template-rows: auto auto auto auto;
      grid-template-columns: 50% 50%;
      grid-template-areas:
        "info booking"
        "impress booking"
        "feedback feedback"
        "rules cancel";
    }
    @include media-breakpoint-down($md) {
      margin: 30px auto 0px;
      grid-template-areas:
        "booking booking"
        "info impress"
        "feedback feedback"
        "rules cancel";
    }
    @include media-breakpoint-down($sm) {
      grid-template-rows: auto auto auto auto auto;
      grid-template-columns: auto;
      grid-template-areas:
        "booking"
        "info"
        "impress"
        "feedback"
        "rules"
        "cancel";
    }
  }

  &__room-info {
    padding-left: 3px;

    &-item {
      margin-top: 25px;
    }

    &-wrapper {
      grid-area: info;
      box-sizing: border-box;
      @include media-breakpoint-between($sm, $lg) {
        margin: 0 20px;
        padding-bottom: $lg-padding-bottom;
      }
      @include media-breakpoint-down($sm) {
        @include padding-sm;
      }
    }
  }

  &__room-impress {
    padding-top: 22px;

    &-wrapper {
      grid-area: impress;
      @include media-breakpoint-between($sm, $lg) {
        margin: 0 20px;
        padding-bottom: $lg-padding-bottom;
      }
      @include media-breakpoint-down($sm) {
        @include padding-sm;
      }
    }
  }

  &__booking {
    &-wrapper {
      grid-area: booking;
      box-sizing: border-box;
      @include media-breakpoint-down($lg) {
        margin: 0 auto;
        padding-bottom: $lg-padding-bottom;
      }
    }
  }

  &__feedback {
    padding-left: 2px;

    &-header {
      display: flex;
      justify-content: space-between;
      @include media-breakpoint-down($xs) {
        display: block;
      }

      &-quantity {
        white-space: nowrap;
        @include font-body;
      }
    }

    &-item {
      margin-top: 22px;
    }

    &-wrapper {
      padding-right: 70px;
      padding-top: 14px;
      grid-area: feedback;
      box-sizing: border-box;
      @include media-breakpoint-between($sm, $lg) {
        padding-bottom: $lg-padding-bottom;
        margin: 0 20px;
        padding-right: 0;
        padding-top: 0;
      }
      @include media-breakpoint-down($sm) {
        @include padding-sm;
      }
    }
  }

  &__rules {
    &-wrapper {
      padding-top: 33px;
      grid-area: rules;
      box-sizing: border-box;
      @include media-breakpoint-between($sm, $lg) {
        padding-bottom: $lg-padding-bottom;
        padding-top: 0;
        margin: 0 20px;
      }
      @include media-breakpoint-down($sm) {
        @include padding-sm;
      }
    }

    &-header {
      padding-left: 21px;
      margin-bottom: 19px;
      @include media-breakpoint-down($xs) {
        padding-left: 0;
      }
    }
  }

  &__cancel {
    @include font-body;
    margin-top: 19px;
    &-wrapper {
      padding-right: 73px;
      padding-top: 32px;
      padding-left: 1px;
      grid-area: cancel;
      box-sizing: border-box;
      @include media-breakpoint-down($sm) {
        @include padding-sm;
      }
      @include media-breakpoint-between($sm, $lg) {
        padding-bottom: $lg-padding-bottom;
        padding-top: 0;
        padding-right: 0;
        margin: 0 20px;
      }
    }
  }
  &__footer {
    border-top: 1px solid $colorDarkShade25;
    padding-top: 100px;
  }
}
