$xxs: 320px;
$xs: 575px;
$sm: 767px;
$md: 991px;
$lg: 1199px;
$xl: 1440px;
$xxl: 1920px;

$headerHeight: 70px;

$colorDarkShade100: #1f2041;
$colorDarkShade75: rgba(31, 32, 65, 0.75);
$colorDarkShade50: rgba(31, 32, 65, 0.5);
$colorDarkShade25: rgba(31, 32, 65, 0.25);
$colorDarkShade15: rgba(31, 32, 65, 0.15);
$colorDarkShade5: rgba(31, 32, 65, 0.05);
$colorRed: #ff0000;
$colorPurple: #bc9cff;
$colorPurpleOpaque: rgba(188, 156, 255, 0.5);
$colorBlue: #8ba4f9;
$colorBlueOpaque: rgba(139, 164, 249, 0.5);
$colorGreen: #6fcf97;
$colorWhite: #ffffff;
$colorLightBlue: #66d2ea;
$gradientPurple: linear-gradient($colorPurple 0%, $colorBlue 100%);
$gradientGreen: linear-gradient($colorGreen 0%, $colorLightBlue 100%);

@mixin font-h1() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: $colorDarkShade100;
}

@mixin font-h2() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 19px;
  line-height: 23px;
  color: $colorDarkShade100;
}

@mixin font-h3() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: $colorDarkShade100;
  text-transform: uppercase;
}

@mixin font-body() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: $colorDarkShade75;
}

@mixin media-breakpoint-up($width) {
  @media (min-width: ($width + 1)) {
    @content;
  }
}

@mixin media-breakpoint-down($width) {
  @media (max-width: $width) {
    @content;
  }
}

@mixin media-breakpoint-between($min-width, $max-width) {
  @media (min-width: ($min-width + 1)) and (max-width: $max-width) {
    @content;
  }
}

html {
  height: 100%;
  width: 100%;
  font-size: 16px;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  width: 100%;
  background: rgb(255, 255, 255);
  min-width: 320px;
}

main {
  flex-grow: 1;
}

fieldset {
  padding: 0;
  margin: 0;
  border: none;
}

form {
  margin-block-end: 0;
}

button {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

image {
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

.date-dropdown {
  user-select: none;
  display: flex;
  justify-content: flex-start;
  position: relative;

  &_width {
    &_wide {
      max-width: 320px;
      width: 100%;
    }

    &_narrow {
      max-width: 266px;
      width: 100%;
    }
  }

  &__label {
    width: 100%;
    position: relative;

    &-text {
      @include font-h3;
    }
  }

  &__calendar-wrapper {
    position: absolute;
    z-index: 3;
    top: 68px;

    &_hidden {
      display: none;
    }
  }

  &__button {
    &-clear {
      position: absolute;
      bottom: 20px;
      left: 20px;
    }

    &-apply {
      position: absolute;
      bottom: 20px;
      right: 20px;
    }
  }

  &__image {
    cursor: pointer;
    position: absolute;
    bottom: 18px;
    right: 16px;

    &_expanded {
      transform: rotate(180deg);
      transition: all 0.3s ease 0s;
    }

    &_collapsed {
      transform: rotate(0deg);
      transition: all 0.3s ease 0s;
    }
  }

  &__input {
    cursor: pointer;
    &::placeholder {
      color: $colorDarkShade75;
    }
    @include font-body;
    height: 44px;
    text-indent: 12px;
    border-radius: 5px;
    border: 1px solid $colorDarkShade25;
    margin-top: 5px;

    &-wrapper {
      display: flex;
      flex-direction: column;
      &:nth-child(2) {
        padding-left: 20px;
      }
    }

    &:hover {
      border-color: $colorDarkShade50;
    }
    &:focus {
      border-color: $colorDarkShade50;
      outline: none;
    }

    &_width {
      &_narrow {
        min-width: 150px;
      }
      &_wide {
        min-width: 266px;
      }
    }

    &::-webkit-datetime-edit-fields-wrapper {
      font-family: "Montserrat", Arial, Helvetica, sans-serif;
      color: $colorDarkShade75;
      text-transform: uppercase;
    }

    &::-webkit-calendar-picker-indicator {
      -webkit-appearance: none;
      display: none;
    }
    &[type="date"] {
      text-indent: 6px;
    }
  }

  .air-datepicker {
    box-sizing: border-box;
    width: 320px;
    &-nav {
      border: none;
      padding: 4px 24px 4px 24px;
      min-height: 51px;
      &--title {
        cursor: inherit;
        font-family: "Montserrat", Arial, Helvetica, sans-serif;
        font-weight: bold;
        font-size: 19px;
        line-height: 23px;
        color: $colorDarkShade100;
        i {
          color: $colorDarkShade100;
        }
        &:hover {
          background: none;
        }
      }
      &--action {
        &[data-action="prev"] {
          background: url("./image/arrow-back.svg") center no-repeat;
        }
        &[data-action="next"] {
          background: url("./image/arrow-forward.svg") center no-repeat;
        }
      }
    }

    &-body {
      &--day {
        &-name {
          color: $colorPurple;
          font-family: "Montserrat", Arial, Helvetica, sans-serif;
          font-weight: bold;
          font-weight: 700;
          font-size: 12px;
          line-height: 14.63px;
          text-transform: capitalize;
        }
      }
      &--cells {
        padding-top: 10px;
        &.-days- {
          grid-template-columns: repeat(7, 40px);
          grid-auto-rows: 39px;
        }
      }
    }

    &-cell {
      height: 39px;
      &.-day- {
        color: $colorDarkShade50;
        font-family: "Montserrat", Arial, Helvetica, sans-serif;
        font-weight: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 14.63px;
      }
    }
    &--content {
      padding: 17px 18px 54px 20px;
    }
    &-cell.-day- {
      &.-disabled- {
        color: $colorDarkShade25;
      }

      &.-current- {
        border-radius: 50%;
        background: $gradientGreen;
      }

      &.-range-from- {
        border: none;
        &::after {
          position: absolute;
          z-index: -1;
          content: "";
          height: 39px;
          width: 40px;
          background: $gradientPurple;
          opacity: 0.5;
          border-bottom-left-radius: 50%;
          border-top-left-radius: 50%;
        }
      }

      &.-range-to- {
        border: none;
        &::before {
          position: absolute;
          z-index: -1;
          content: "";
          height: 39px;
          width: 40px;
          background: $gradientPurple;
          opacity: 0.5;
          border-bottom-right-radius: 50%;
          border-top-right-radius: 50%;
        }
      }

      &.-focus- {
        font-weight: 700;
        position: relative;
        border-radius: 50%;
        background: $gradientPurple;
        &.-disabled- {
          background: none;
        }
      }

      &.-selected- {
        font-weight: 700;
        position: relative;
        border-radius: 50%;
        background: $gradientPurple;
      }

      &.-focus-.-selected- {
        background: $gradientPurple;
        font-weight: 700;
      }

      &.-in-range- {
        background: $gradientPurple;
        opacity: 0.5;
      }

      &.-in-range-.-focus- {
        position: relative;
        border-radius: 50%;
        background: $gradientPurple;
        opacity: 1;
        &::before {
          position: absolute;
          content: "";
          height: 39px;
          width: 40px;
          background: $gradientPurple;
          opacity: 0.5;
        }
      }
    }
  }

  &_width_narrow {
    .air-datepicker {
      width: 266px;
      &-body {
        &--cells {
          &.-days- {
            grid-template-columns: repeat(7, 32px);
            grid-auto-rows: 32px;
          }
        }
      }

      &-cell {
        height: 32px;
      }
      &-cell.-day- {
        &.-range-from-::after {
          height: 32px;
          width: 32.2px;
        }
        &.-range-to-::before {
          height: 32px;
          width: 32.2px;
        }
        &.-in-range-.-focus-::before {
          height: 32px;
          width: 32.27px;
        }
      }
    }
  }
}
