$xxs: 320px;
$xs: 575px;
$sm: 767px;
$md: 991px;
$lg: 1199px;
$xl: 1440px;
$xxl: 1920px;

$headerHeight: 70px;

$colorDarkShade100: #1f2041;
$colorDarkShade75: rgba(31, 32, 65, 0.75);
$colorDarkShade50: rgba(31, 32, 65, 0.5);
$colorDarkShade25: rgba(31, 32, 65, 0.25);
$colorDarkShade15: rgba(31, 32, 65, 0.15);
$colorDarkShade5: rgba(31, 32, 65, 0.05);
$colorRed: #ff0000;
$colorPurple: #bc9cff;
$colorPurpleOpaque: rgba(188, 156, 255, 0.5);
$colorBlue: #8ba4f9;
$colorBlueOpaque: rgba(139, 164, 249, 0.5);
$colorGreen: #6fcf97;
$colorWhite: #ffffff;
$colorLightBlue: #66d2ea;
$gradientPurple: linear-gradient($colorPurple 0%, $colorBlue 100%);
$gradientGreen: linear-gradient($colorGreen 0%, $colorLightBlue 100%);

@mixin font-h1() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: $colorDarkShade100;
}

@mixin font-h2() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 19px;
  line-height: 23px;
  color: $colorDarkShade100;
}

@mixin font-h3() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: $colorDarkShade100;
  text-transform: uppercase;
}

@mixin font-body() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: $colorDarkShade75;
}

@mixin media-breakpoint-up($width) {
  @media (min-width: ($width + 1)) {
    @content;
  }
}

@mixin media-breakpoint-down($width) {
  @media (max-width: $width) {
    @content;
  }
}

@mixin media-breakpoint-between($min-width, $max-width) {
  @media (min-width: ($min-width + 1)) and (max-width: $max-width) {
    @content;
  }
}

html {
  height: 100%;
  width: 100%;
  font-size: 16px;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  width: 100%;
  background: rgb(255, 255, 255);
  min-width: 320px;
}

main {
  flex-grow: 1;
}

fieldset {
  padding: 0;
  margin: 0;
  border: none;
}

form {
  margin-block-end: 0;
}

button {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

image {
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

.search-room-page {
  box-sizing: border-box;
  display: flex;
  margin: 0 auto;
  padding-top: 31px;
  padding-left: 40px;
  padding-right: 40px;

  @include media-breakpoint-between($lg, $xl) {
    padding-left: 0;
    padding-right: 0;
  }

  @include media-breakpoint-down($lg) {
    flex-direction: column;
    padding-left: 20px;
    padding-right: 20px;
  }

  @include media-breakpoint-down($sm) {
    padding-left: 0;
    padding-right: 0;
  }

  &__filter-form-wrapper {
    @include media-breakpoint-down($sm) {
    }
  }

  &__title {
    display: flex;
    @include font-h1;
    margin-bottom: 20px;
    text-align: center;
    @include media-breakpoint-down($md) {
      display: block;
      font-size: 1.25rem;
    }
    @include media-breakpoint-down($sm) {
      font-size: 1rem;
    }
  }

  &__search-results {
    max-width: 835px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 9px;
    grid-auto-rows: 268px;
    @include media-breakpoint-down($md) {
      grid-template-columns: repeat(2, 1fr);
    }
    @include media-breakpoint-down($xs) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    &-wrapper {
      max-width: 100%;
      box-sizing: border-box;
      padding-left: 59px;
      @include media-breakpoint-down($lg) {
        padding: 0;
      }
      @include media-breakpoint-down($xs) {
        padding: 32px 0 0 0;
      }
    }
  }

  &__room-card {
    margin-bottom: 19px;
  }

  &__pagination-wrapper {
    margin-top: 32px;
    margin-bottom: 74px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__go-up-button-wrapper {
    position: fixed;
    right: 20px;
    bottom: 50px;
    z-index: 10;
    @include media-breakpoint-up($xs) {
      display: none;
    }
  }
  &__footer {
    border-top: 1px solid $colorDarkShade25;
    padding-top: 100px;
  }
}
