$xxs: 320px;
$xs: 575px;
$sm: 767px;
$md: 991px;
$lg: 1199px;
$xl: 1440px;
$xxl: 1920px;

$headerHeight: 70px;

$colorDarkShade100: #1f2041;
$colorDarkShade75: rgba(31, 32, 65, 0.75);
$colorDarkShade50: rgba(31, 32, 65, 0.5);
$colorDarkShade25: rgba(31, 32, 65, 0.25);
$colorDarkShade15: rgba(31, 32, 65, 0.15);
$colorDarkShade5: rgba(31, 32, 65, 0.05);
$colorRed: #ff0000;
$colorPurple: #bc9cff;
$colorPurpleOpaque: rgba(188, 156, 255, 0.5);
$colorBlue: #8ba4f9;
$colorBlueOpaque: rgba(139, 164, 249, 0.5);
$colorGreen: #6fcf97;
$colorWhite: #ffffff;
$colorLightBlue: #66d2ea;
$gradientPurple: linear-gradient($colorPurple 0%, $colorBlue 100%);
$gradientGreen: linear-gradient($colorGreen 0%, $colorLightBlue 100%);

@mixin font-h1() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: $colorDarkShade100;
}

@mixin font-h2() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 19px;
  line-height: 23px;
  color: $colorDarkShade100;
}

@mixin font-h3() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: $colorDarkShade100;
  text-transform: uppercase;
}

@mixin font-body() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: $colorDarkShade75;
}

@mixin media-breakpoint-up($width) {
  @media (min-width: ($width + 1)) {
    @content;
  }
}

@mixin media-breakpoint-down($width) {
  @media (max-width: $width) {
    @content;
  }
}

@mixin media-breakpoint-between($min-width, $max-width) {
  @media (min-width: ($min-width + 1)) and (max-width: $max-width) {
    @content;
  }
}

html {
  height: 100%;
  width: 100%;
  font-size: 16px;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  width: 100%;
  background: rgb(255, 255, 255);
  min-width: 320px;
}

main {
  flex-grow: 1;
}

fieldset {
  padding: 0;
  margin: 0;
  border: none;
}

form {
  margin-block-end: 0;
}

button {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

image {
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

$borderWidth: 2px;

.button {
  cursor: pointer;
  margin: 0;
  padding: 0 10px 0 10px;
  position: relative;
  @include font-h3;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  user-select: none;
  border-radius: 22px;

  &_length {
    &_very-long {
      max-width: 320px;
      width: 100%;
    }

    &_long {
      max-width: 196px;
      width: 100%;
    }

    &_short {
      max-width: 99px;
      width: 100%;
    }
    &_very-short {
      max-width: 87px;
      width: 100%;
    }
  }

  &_width {
    &_wide {
      height: 44px;
    }

    &_narrow {
      height: 34px;
    }
  }

  &_type_plain-text {
    color: $colorPurple;
    border: none;
    background: none;
    &:hover,
    &:focus {
      color: $colorDarkShade50;
    }
  }

  &_color {
    &_dark {
      background: $gradientPurple;
      color: $colorWhite;
      &:hover,
      &:focus {
        opacity: 0.5;
      }
    }

    &_light {
      background-color: $colorWhite;
      box-shadow: inset 0px 2px 0px 0px $colorPurple, inset 0px 0px 0px 2px $colorBlue;
      color: $colorPurple;
      &:hover,
      &:focus {
        box-shadow: inset 0px 2px 0px 0px $colorPurpleOpaque, inset 0px 0px 0px 2px $colorBlueOpaque;
      }
    }
  }

  &__image {
    position: absolute;
    top: 14px;
    right: 14px;
  }
}
