$xxs: 320px;
$xs: 575px;
$sm: 767px;
$md: 991px;
$lg: 1199px;
$xl: 1440px;
$xxl: 1920px;

$headerHeight: 70px;

$colorDarkShade100: #1f2041;
$colorDarkShade75: rgba(31, 32, 65, 0.75);
$colorDarkShade50: rgba(31, 32, 65, 0.5);
$colorDarkShade25: rgba(31, 32, 65, 0.25);
$colorDarkShade15: rgba(31, 32, 65, 0.15);
$colorDarkShade5: rgba(31, 32, 65, 0.05);
$colorRed: #ff0000;
$colorPurple: #bc9cff;
$colorPurpleOpaque: rgba(188, 156, 255, 0.5);
$colorBlue: #8ba4f9;
$colorBlueOpaque: rgba(139, 164, 249, 0.5);
$colorGreen: #6fcf97;
$colorWhite: #ffffff;
$colorLightBlue: #66d2ea;
$gradientPurple: linear-gradient($colorPurple 0%, $colorBlue 100%);
$gradientGreen: linear-gradient($colorGreen 0%, $colorLightBlue 100%);

@mixin font-h1() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: $colorDarkShade100;
}

@mixin font-h2() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 19px;
  line-height: 23px;
  color: $colorDarkShade100;
}

@mixin font-h3() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: $colorDarkShade100;
  text-transform: uppercase;
}

@mixin font-body() {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: $colorDarkShade75;
}

@mixin media-breakpoint-up($width) {
  @media (min-width: ($width + 1)) {
    @content;
  }
}

@mixin media-breakpoint-down($width) {
  @media (max-width: $width) {
    @content;
  }
}

@mixin media-breakpoint-between($min-width, $max-width) {
  @media (min-width: ($min-width + 1)) and (max-width: $max-width) {
    @content;
  }
}

html {
  height: 100%;
  width: 100%;
  font-size: 16px;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  width: 100%;
  background: rgb(255, 255, 255);
  min-width: 320px;
}

main {
  flex-grow: 1;
}

fieldset {
  padding: 0;
  margin: 0;
  border: none;
}

form {
  margin-block-end: 0;
}

button {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

image {
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

$shift-horizontal: 25px;
$shift-vertical: 14px;

.room-card {
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  background-color: $colorWhite;
  @include media-breakpoint-between($xxs, $xs) {
    width: calc(100vw - #{$shift-horizontal});
    min-width: 100%;
  }

  &__slider {
    position: relative;
    min-width: 270px;
    min-height: 152px;
    @include media-breakpoint-between($xxs, $xs) {
      height: calc(56.3vw - #{$shift-vertical});
      height: 56.3vw;
      width: auto;
    }
  }

  &__photos::before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: "";
    z-index: 2;
  }

  &__photo {
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    position: absolute;
    left: 0;
    opacity: 0;
    transition: opacity 1s;
    z-index: 1;

    &_shown {
      opacity: 1;
    }
    @include media-breakpoint-between($xxs, $xs) {
      height: 56.3vw;
    }
  }

  &__previous,
  &__next {
    border-top-left-radius: 4px;
    cursor: pointer;
    margin: 0;
    padding: 0;
    top: 0px;
    bottom: 0px;
    width: 16%;
    background: none;
    position: absolute;
    z-index: 2;
    &-image {
      display: none;
    }
    &:hover,
    &:focus {
      border-top-left-radius: 4px;
      background: linear-gradient(90deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 99.99%, rgba(255, 255, 255, 0) 100%);
    }
    &:hover &-image,
    &:focus &-image {
      display: inline;
    }
  }

  &__previous {
    bottom: 0px;
    &-image {
      left: 20px;
    }
  }

  &__next {
    right: 0px;
    &:hover,
    &:focus {
      transform: matrix(-1, 0, 0, 1, 0, 0);
    }
    &-image {
      right: 20px;
    }
  }

  &__dots {
    display: flex;
    justify-content: space-between;
    position: absolute;
    z-index: 3;
    bottom: 16px;
    right: 15px;
  }

  &__dot {
    margin-left: 3px;
    width: 8px;
    height: 8px;
    border: 1px solid $colorWhite;
    border-radius: 50%;
    cursor: pointer;
    &:hover,
    &:focus {
      background-color: $colorWhite;
    }
    &_active {
      background-color: $colorWhite;
    }
  }

  &__details {
    padding-left: 18px;
    padding-right: 18px;
    margin-top: 17px;
    display: flex;
    justify-content: space-between;
    &-room {
      flex-grow: 1;
      border-bottom: 1px solid rgba(31, 32, 65, 0.1);
      padding-bottom: 10px;
      margin-top: 2px;
    }

    &-price {
      border-bottom: 1px solid rgba(31, 32, 65, 0.1);
      margin-top: 3px;
    }
  }

  &__detail {
    &-mark {
      @include font-h3;
      font-size: 14px;
      padding-left: 2px;
    }

    &-number {
      @include font-h2;
      padding-left: 4px;
    }

    &-lux {
      @include font-h3;
      color: $colorPurple;
      padding-left: 4px;
    }

    &-price {
      @include font-body;
      font-weight: bold;
      color: $colorDarkShade50;

      &-currency {
        @include font-body;
        font-weight: bold;
        color: $colorDarkShade50;
      }
    }

    &-label {
      @include font-body;
      font-size: 12px;
      color: $colorDarkShade50;
      padding-left: 3px;
    }
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    margin-top: 11px;
    padding-left: 22px;
    padding-right: 20px;
  }

  &__feedback {
    @include font-body;
    margin-bottom: 18px;
    &-amount {
      font-weight: bold;
      color: $colorDarkShade50;
    }
    &-mark {
      padding-left: 4px;
    }
  }
}
